:root {
  --link-on: #00acdc;
  --link-off: black;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  height: 60px;
  padding: 0 50px 0 30px;
  display: flex;
}

a.imgs {
  align-self: center;
  margin-left: 0;
  display: block;
}

.navigation {
  align-items: center;
  display: flex;
}

.nav_list {
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding-inline-start: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  align-items: center;
  height: 100%;
  display: flex;
}

a:link {
  color: var(--link-off);
  text-transform: uppercase;
  align-items: center;
  height: 100%;
  margin-left: 20px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
  display: flex;
}

a:visited {
  color: var(--link-off);
}

a:hover {
  color: var(--link-on);
}

a:active {
  color: var(--link-on);
  position: relative;
}

a.is-active:link {
  color: var(--link-on);
  margin-left: 0;
  position: relative;
}

a.is-active:visited, a.is-active:hover, a.is-active:active {
  color: var(--link-on);
  position: relative;
}

a:active:after, a.is-active:after {
  content: "";
  border-bottom: 4px solid var(--link-on);
  border-radius: 2px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

